import React from 'react';

import ContentPage from '../../components/content-page';
import Meta from '../../components/meta';
import OutboundLink from '../../components/outbound-link';
import Wrapper from '../../components/wrapper';

// import unaImagen from './img/una-imagen.png';


import styles from './styles.module.css';

const About = ({ location }) => (
  <ContentPage>
    <Meta title="About" location={location} />
    <Wrapper>
      <div className={styles.about}>
        <section className={styles.intro}>
          <h1>Hola mundo.</h1>
          <p>Soy un programador iOS y web en Lugo. Tengo más de diez años de experiencia en el desarrollo de software y cinco en el desarrollo de aplicaciones para iOS.</p>
          <p>Actualmente estoy trabajando en <OutboundLink to="http://www.ailalelo.com">Ailalelo</OutboundLink> como programador.</p>
        </section>
        <aside className={styles.skills}>
          <div>
            <h3>Habilidades</h3>
            <h4>Lenguajes de programación</h4>
            <p>Swift, Objective-C, JavaScript, Python, PHP</p>
            <h4>iOS</h4>
            <p>Swift Standard Library, Foundation, UIKit, Objective-C, Core Data, Core Foundation, MapKit, Core Location, Notificacion Center, UserNotifications, Core Graphics, Core Animation, Core Bluetooth, XCTest, Carthage, CocoaPods</p>
            <h4>Web</h4>
            <p>Express, Node, Django</p>
            <h4>Otros</h4>
            <p>C, C++, SQL, Git, Svn</p>
          </div>

        </aside>
      </div>

      {/* <section>
        <h2>Qué dice la gente</h2>
        <div className={styles.referrals}>
          <div className={styles.referral}>
            <img src={unaImagen} alt="Testimonio logo" />
            <div>
              <h4>[Persona], [Empresa]</h4>
              <p>“[testimonio]”</p>
            </div>
          </div>
        </div>
      </section> */}
    </Wrapper>
  </ContentPage>
);

export default About;



